@import url(./styles/normalize.css);
@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');
@import url(./styles/nav.css);

@font-face {
    font-family: 'VT323', Regular;
    src: url("/assets/VT323/VT323-Regular.ttf");
}

canvas, #canvas_img {
    width: 1280px;
    height: 620px;
    position: absolute;
    top: 50%;
    left: 50%;
    align-content: center;
    transform: translate(-50%, -50%);
    border: 2px solid black;
}

body-container {
    z-index: 1;
}
canvas {
    z-index: 2;
}

// splash {
    
// }

#body-container {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1400px;
    height: 800px;
    align-content: center;
    transform: translate(-50%, -50%);
}
#audio_button {
    z-index: 5;
    position: absolute;
    top: 9%;
    left: 91%;
    text-align: center;
    transform: translate(-50%, -50%);
}
#audio_button:hover {
    color: pink;
}
.splash {
    position: absolute;
    top: 55%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);
    font-size: 20px;
}

#splash-instructions {
    z-index: 3;
}

#end-screen {
    z-index: 4;
    display: none;
}

h1 {
    font-size: 50px;
    top: 20%;
    display: flex;
    justify-content: center;
}

// #splash-instructions p {
//     font-size: 20px;
// }

button {
    background-color: transparent;
    // background-repeat:no-repeat;
    border: none;
    font-size: 25px;

}
.game-button:hover {
    background-color: pink;
    transform: scale(1.5);
}

#background_img {
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.2;
}


